.App {
  text-align: center;
  min-height: 100vh;
}

.App-Main, .App-Account {
  margin: 0 15vw;
}

/* Header Styling */
.App-Header {
  padding: 50px 18vw 10px;
  display: flex;
  justify-content: space-between;
}

.Header-Navbar {
  display: flex;
}

.Header-Link {
  padding: 5px 10px;
  font-size: 1.75em;
  color: #000;
  text-decoration: unset;
}

.Header-Link-Selected {
  color: silver;
}

.Header-Link:hover {
  color: #AF6FC3;
}

/* Table Component Styling */
.Table {
  margin: 15px 0;
  background-color: #eaeaea;
  border: 1px solid silver;
  border-radius: 15px;
  box-shadow: 3px 3px 5px 1px rgb(0 0 0 / 20%);
}

.Table-Title {
  padding-left: 15px;
  font-size: 1.15em;
  text-align: left;
}

/* .Table-Head, .Table-Body{} */

.Table-Head {
  border-bottom: 1px solid silver;
}

/* .Table-Head-Row, .Table-Body-Row{} */

.Table-Head-Data, .Table-Body-Data {
  padding: 3px 10px;
  vertical-align: top;
  text-align: left;
  font-size: 0.8em;
}

